import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import Select from "react-select";
import {
  FaBook,
  FaCode,
  FaExchangeAlt,
  FaPercent,
  FaPlus,
  FaSync,
  FaTextWidth,
  FaTrash,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import queryString from "query-string";
import { Formik } from "formik";
import * as Yup from "yup";

import "./EvaluationTypeEdit.css";

import { RoutesString } from "pages/routesString";
import evaluationTypeAPI from "api/common/evaluation-type";
import subjectOutputStandardAPI from "api/common/subject-output-standard";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import PageHeader from "./components/PageHeader/PageHeader";
import syllabusAPI from "api/common/syllabus";
import i18n from "i18n/i18n";

export default function EvaluationTypeEdit() {
  const { t } = useTranslation();

  const history = useHistory();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [initialValues, setInitialValues] = useState({
    ma: "",
    ten: "",
    tyLe: "",
    idSyllabus: "",
    chuanDauRaMonHoc: [],
  });
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [subjectOutputStandards, setSubjectOutputStandards] = useState([]);
  const [syllabuses, setSyllabuses] = useState([]);

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    }
    setIsLoading(true);
    try {
      const subjectOutputStandardsRes = await subjectOutputStandardAPI.getAll();
      const subjectOutputStandardsOptions = subjectOutputStandardsRes.contents.map(
        (item) => {
          return { value: item.id, label: item.ma };
        }
      );
      setSubjectOutputStandards(subjectOutputStandardsOptions);
      const syllabusesRes = await syllabusAPI.getAll();
      setSyllabuses(syllabusesRes.contents);
      if (!parsed.id) {
        setInitialValues({
          ...initialValues,
          idSyllabus: syllabusesRes.contents[0].id,
        });
      }
    } catch (err) {// eslint-disable-next-line
      
    } finally {
      setIsLoading(false);
    }
  }, []);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await evaluationTypeAPI.getById(id);
      setInitialValues({
        ma: res.ma,
        ten: res.ten,
        tyLe: res.tyLe,
        chuanDauRaMonHoc: res.chuanDauRaMonHoc.map((item) => ({
          value: item.id,
          label: item.ma,
        })),
        idSyllabus: res.syllabus,
      });
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403) {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditOnClick = async function (values, isValid) {
    if (!isValid) {
      return;
    }
    if (isUpdatePage) {
      const body = {
        ...values,
        chuanDauRaMonHoc: values.chuanDauRaMonHoc.map((item) =>
          item.value.toString()
        ),
        idSyllabus: parseInt(values.idSyllabus),
      };
      try {
        const res = await evaluationTypeAPI.putById(id, body);
        if (res.status === 200) {
          setNotification({
            color: "success",
            isOpen: true,
            messages: [t("admin.UPDATE_SUCCESS")],
          });
          updateForm(id);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403) {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    } else {
      const body = {
        ...values,
        chuanDauRaMonHoc: values.chuanDauRaMonHoc.map((item) =>
          item.value.toString()
        ),
        idSyllabus: parseInt(values.idSyllabus),
      };
      try {
        const res = await evaluationTypeAPI.post(body);
        if (res.id) {
          setNotification({
            color: "success",
            isOpen: true,
            messages: [t("admin.ADD_SUCCESS")],
          });
          history.push(
            `${RoutesString.ADMIN_EVALUATION_TYPE_EDIT}?id=${res.id}`
          );
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403) {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    }
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    const DELETE_SUCCESS_CODE = 200;
    try {
      const res = await evaluationTypeAPI.deleteById(id);
      if (String(res?.message) === 'DELETE_LOAIDANHGIA_SUCCESSFULLY') {
        toast.success(t("admin.common.DELETED"));
        history.push(`${RoutesString.ADMIN_EVALUATION_TYPE_LIST}`);

      } else {
        setNotification({
          color: "danger",
          isOpen: true,
          messages: [t("ERROR")],
        });
      }
    } catch (err) {
      if (err?.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
            toast.error(t("ER_DUP_ENTRY"));
          } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.evaluation_type_edit.UPDATE_EVALUATION_TYPE")
            : t("admin.evaluation_type_edit.ADD_EVALUATION_TYPE")
        }
        items={[
          {
            href: RoutesString.ADMIN_EVALUATION_TYPE_LIST,
            title: t("admin.evaluation_type_list.EVALUATION_TYPE_LIST"),
          },
        ]}
      />

      <PageHeader
        titleHeader={
          isUpdatePage
            ? t("admin.evaluation_type_edit.UPDATE_EVALUATION_TYPE")
            : t("admin.evaluation_type_edit.ADD_EVALUATION_TYPE")
        }
      />

      <div className="page-content">
        <Formik
          initialValues={initialValues}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
            ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
            tyLe: Yup.number().required(i18n.t("REQUIRED_FIELD_WARNING")).min(0, i18n.t("admin.POSITIVE_WARNING")),
          })}
        >
          {({
            values,
            errors,
            dirty,
            isValid,
            handleChange,
            handleReset,
            setFieldValue,
          }) => (
            <Form>
              <Notification
                color={notification.color}
                isOpen={notification.isOpen}
                messages={notification.messages}
              />
              <FormGroup className="row">
                <Label for="ma" className="col-lg-3">
                  {t("admin.evaluation_type_edit.CODE")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaCode />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="ma"
                      placeholder={t(
                        "admin.evaluation_type_edit.CODE_PLACEHOLDER"
                      )}
                      value={values.ma}
                      onChange={handleChange}
                      invalid={errors.ma}
                      readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.ma && <FormText color="danger">{errors.ma}</FormText>}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="ten" className="col-lg-3">
                  {t("admin.evaluation_type_edit.NAME")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaTextWidth />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="ten"
                      placeholder={t(
                        "admin.evaluation_type_edit.NAME_PLACEHOLDER"
                      )}
                      value={values.ten}
                      onChange={handleChange}
                      invalid={errors.ten}
                    />
                  </InputGroup>
                  {errors.ten && (
                    <FormText color="danger">{errors.ten}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="tyLe" className="col-lg-3">
                  {t("admin.evaluation_type_edit.PERCENT")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaPercent />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="number"
                      name="tyLe"
                      placeholder={t(
                        "admin.evaluation_type_edit.PERCENT_PLACEHOLDER"
                      )}
                      value={values.tyLe}
                      onChange={handleChange}
                      invalid={errors.tyLe}
                    />
                  </InputGroup>
                  {errors.tyLe && (
                    <FormText color="danger">{errors.tyLe}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="idSyllabus" className="col-lg-3">
                  {t("Syllabus")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaBook />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="idSyllabus"
                      value={values.idSyllabus}
                      onChange={handleChange}
                    >
                      {syllabuses &&
                        syllabuses.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {`${item.monHoc.ma} - ${item.heDaoTao.ten} - ${item.namHoc.ten}`}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="chuanDauRaMonHoc" className="col-lg-3">
                  {t("admin.evaluation_type_edit.SUBJECT_OUTPUT_STANDARD")}
                </Label>
                <div className="col-lg-6">
                  <Select
                    value={values.chuanDauRaMonHoc}
                    onChange={(e) =>
                      setFieldValue(
                        "chuanDauRaMonHoc",
                        Array.isArray(e) ? e : []
                      )
                    }
                    isMulti
                    closeMenuOnSelect={false}
                    options={subjectOutputStandards}
                    placeholder={t(
                      "admin.evaluation_type_edit.SUBJECT_OUTPUT_STANDARD_PLACEHOLDER"
                    )}
                  />
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup>
                <Button
                  color="success"
                  disabled={isUpdatePage ? !isValid : !(isValid && dirty)}
                  onClick={() =>
                    handleEditOnClick(
                      values,
                      isUpdatePage ? isValid : isValid && dirty
                    )
                  }
                >
                  {isUpdatePage ? (
                    <>
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </>
                  ) : (
                    <>
                      <FaPlus /> {t("admin.common.ADD")}
                    </>
                  )}
                </Button>
                &emsp;
                {isUpdatePage && (
                  <>
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleDeleteOnClick}>
                      <FaTrash /> {t("admin.common.DELETE")}
                    </Button>
                  </>
                )}
              </FormGroup>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
